.um-page {
    &.um-page-login, &.um-page-register {
        @media (min-width: 782px) {
            background: linear-gradient(
                to right, 
                var(--background-secondary) 0%, 
                var(--background-secondary) 50%, 
                var(--background-primary) 50%, 
                var(--background-primary) 100%
            );
            .container {
                background: linear-gradient(
                    to right, 
                    var(--background-secondary) 0%, 
                    var(--background-secondary) 50%, 
                    var(--background-primary) 50%, 
                    var(--background-primary) 100%
                );
            }
        }
        @media (max-width: 781px) {
            background: var(--background-secondary);
            .container {
                background: var(--background-secondary);
            }
        }
    }
        
    // Login Page
    &.um-page-login {
        max-width: 100% !important;
        margin-bottom: 0px !important;
        .um-col-alt {
            .um-half {
                &.um-right {
                    &::before {
                        content: "don’t have an account?";
                    }
                }
            }
        }
    }

    // Register Page 
    &.um-page-register {
        max-width: 100% !important;
        .um-col-alt {
            .um-half {
                &.um-right {
                    &::before {
                        content: "already have an account?";
                    }
                }
            }
        }
        .um-toggle-gdpr {
            display: none !important;
        }
    }

    // Password Reset 
    &.um-page-password-reset {
        background: var(--background-secondary);
        .container {
            background: var(--background-secondary);
        }
        .login-wrapper {
            max-width: 424px;
        }
        .um-button {
            width: 100% !important;
            min-width: 100% !important;
        }
        .um-col-alt-b {
            padding-top: 10px;
        }
        .um-um_password_id {
            margin-bottom: 0 !important;
        }
    }

    .slider-column {
        position: relative;
        @media (max-width: 781px) {
            padding-top: 40px; 
            padding-bottom: 40px;
            &::before {
                content: "";
                display: block;
                width: 100vw;
                height: 100%;
                background-color: var(--background-primary);
                position: absolute;
                top: 0;
                left: -30px;
            }
        }
    }
}


.um {
    &.um-profile {
        &.um-viewing, &.um-editing {
            .um-field-label {
                display: block;
                margin: 0;
                border-bottom: none;
                padding-bottom: 0;
            }
            .um-col-alt {
                .um-field-c {
                    margin-bottom: 15px;
                }
                .um-half {
                    width: 100%;
                    margin-bottom: 15px;
                    &.um-right {
                        position: relative;
                        &::before {
                            content: "";
                            display: block;
                            max-width: 140px;
                            font-size: 10px;
                            font-weight: 400; 
                            text-align: center;
                            color: var(--text-muted); 
                            background-color: var(--background-primary);
                            margin: 0 auto 15px;
                            position: relative;
                            z-index: 2;
                        }
                        &::after {
                            content: "";
                            display: block;
                            width: 100%;
                            height: 1px;
                            background-color: var(--input-border-color);
                            position: absolute;
                            top: 12px;
                            left: 0;
                            z-index: 1;
                        }
                    }
                }
            }
            .um-col-alt-b {
                padding-top: 0px;
                .um-link-alt {
                    color: var(--text-muted) !important;
                    font-size: 16px;
                    font-weight: 700 !important;
                    line-height: 14px !important;
                }
            }
            .um-field-block {
                color: var(--body);
                font-size: 16px;
            }
            .um-form {
                form {
                    .um-field-label {
                        label {
                            font-size: 14px !important;
                            font-style: normal;
                            font-weight: 500;
                            line-height: normal !important;
                        }
                    }
                    input[type=password], input[type=search], input[type=tel], input[type=text], textarea {
                        background: var(--background-secondary) !important;
                        border-radius: 6px !important; 
                        border: 1px solid var(--background-primary) !important;
                        box-shadow: var(--input-shadow) !important; 
                        padding: 16px !important;
                        font-size: 13px !important;
                        font-weight: 500 !important;
                        line-height: 13px !important;
                    }
                    .um-field-checkbox {
                        font-size: 12px;
                        font-weight: 400;
                        line-height: 20px;
                        color: var(--text-body-light);
                        a {
                            text-decoration: none;
                            font-weight: 700;
                            color: var(--text-body-light);
                        }
                    }
                    .um-button { 
                        background: var(--accent-1);
                        border: 1px solid var(--accent-1);
                        color: var(--background-secondary);
                        padding: 16px 24px;
                        border-radius: 8px !important;
                        font-size: 16px; 
                        font-weight: 700 !important;
                        line-height: 14px !important;
                        &:hover, &:focus, &:active {
                            background: var(--accent-1-dark);
                            border: 1px solid var(--accent-1-dark);
                            color: var(--background-secondary);
                        }
                        &.um-alt {
                            background: var(--background-primary);
                            border: 1px solid var(--accent-1) !important;
                            color: var(--accent-1);
                            box-shadow: none;
                            &:hover, &:focus, &:active {
                                background: var(--accent-1);
                                border: 1px solid var(--accent-1) !important;
                                color: var(--background-primary);
                            }
                        }
                    }
                    .um-field-error, .um-notice.err {
                        background: var(--warning);
                        font-size: 12px;
                        font-weight: 400;
                        line-height: 16px;
                        padding: 5px !important;
                        .um-field-arrow {
                            color: var(--warning);
                        }
                    }
                    .um-notice.err {
                        padding-right: 30px !important; 
                        i {
                            font-size: 27px;
                            top: 1px;
                        }
                    }
                    @media (min-width: 781px) {
                        .um-col-121 {
                            float: left;
                            width: 48%;
                        }
                        .um-col-122 {
                            float: right;
                            width: 48%;
                        }
                        .um-col-131 {
                            float: left;
                            width: 30%;
                        }
                        .um-col-132 {
                            float: left;
                            width: 30%;
                            margin: 0 5%;
                        }
                        .um-col-133 {
                            float: left;
                            width: 30%;
                        }
                    }
                }
            }
            // .um-form {
            //     .um-row {
            //         display: flex;
            //         flex-wrap: wrap;
            //         margin-right: -25px;
            //         margin-left: -25px;
            
            //         [class^='um-col-'] {
            //             position: relative;
            //             width: 100%;
            //             min-height: 1px;
            //             padding-right: 25px;
            //             padding-left: 25px;
            //             flex-grow: 1;
            //             flex-basis: 0;
            //             max-width: 100%;
            //         }
            //     }
            // }
        }
        
    }
}

.login-wrapper {
    background-color: var(--background-primary);
    border: 1px solid #EDF2F7;
    border-radius: 16px;
    max-width: 500px;
    width: 100%;
    padding: 40px;
    margin: 0 0 30px;
    margin: 0 auto;
    box-shadow: var(--input-shadow);
}